* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

html {
  font-size: 16.5px;
}

body {
  background: #000000;
}

.app {
  display: flex;
  justify-content: center;
  /*margin: 3rem;*/
}

.app-header {
  text-align: center;
}

.title {
  color: #738f93;
  font-size: 4rem;
  font-family: fantasy, sans-serif;
  margin: 2rem 0;
}

.img-home {
  width: 45rem;
  padding: 4rem;
  margin: 10px auto;
}

.btn-search {
  display: block;
  margin: 0 auto;
  background-color: #738f93;
  color: #ffffff;
  box-shadow: 0 4px 12px rgba(94, 95, 95, 0.5);
  font-size: 1.8rem;
  padding: 0 .5rem;
  min-height: 2.7rem;
  border: none;
  cursor: pointer;
}

.btn-search:hover {
  background-color: #ffffff;
  border: 1px solid #738f93;
  color: #000000;
  font-weight: bold;
  transform: scale(1.0);
}

@media screen and (max-width: 1024px) {

  body {
    max-width: 1024px;
    max-height: 876px;
  }

  .title {
    font-size: 3.5rem;
    margin-bottom: 1.5rem;
  }


  .img-home {
    width: 45rem;
    padding: 4rem;
    margin: 0 auto;
  }
}

/*--------Responsive---TABLET-----*/

@media screen and (max-width: 767px) {


  header {
    width: 100%;
  }

  .title {
    font-size: 3.5rem;
    margin-bottom: 1.5rem;
  }

  .app {
    margin: 1rem auto;
  }

  .img-home {
    width: 100%;
    padding: 4rem;
    margin: 0 auto;
  }
}
/*--------Responsive---CELULAR-----*/


@media screen and (max-width:412px) {
  body {
    width: 100%;
  }

  .app {
    width: 100%;
    height: 100%;
    margin: 3rem 0;
  }

  .title {
    font-size: 2.5rem;
  }

  .img-home {
    width: 100%;
    margin: 0 auto;
  }

  .btn-search {
    box-shadow: 0 4px 12px rgba(94, 95, 95, 0.5);
    font-size: 1.6rem;
  }
}

/*--------Responsive---CELULAR-----*/

@media screen and (max-width:375px) {
  body {
    width: 100%;
  }

  .app {
    width: 100%;
    height: 100%;
    margin: 5rem 0;
  }

  .title {
    font-size: 2.5rem;
    margin:  0;
  }

  .img-home {
    width: 100%;
    margin: 0 auto;
  }

  .btn-search {
    box-shadow: 0 4px 12px rgba(94, 95, 95, 0.5);
    font-size: 1.4rem;
  }
}